import React, { useState, useEffect } from 'react';
import { ResponsiveContainer, Treemap } from 'recharts';
import { get_dividends_rate } from './utils';
import { useParams } from 'react-router-dom';

export default function MarketAnalysis() {
  const [data, setData] = useState<{ name: string, dividends: number }[]>([]);
  const params: { market: string } = useParams()

  useEffect(() => {
    get_dividends_rate(params.market).then(response => {
      let info = response as { ticker: string; dividendRate: number }[]
      const newArray = info.map(item => {
        let pc = Number((item.dividendRate * 100).toFixed(2))
        return { name: `${item.ticker} ${pc}%`, dividends: pc };
      });

      setData(newArray.sort(function (a, b) {
        var x = a["dividends"]; var y = b["dividends"];
        return ((x > y) ? -1 : ((x < y) ? 1 : 0));
      }))
    })
  }, [params.market])

  return (
    <div style={{ width: 800, height: 600 }}>
      <ResponsiveContainer width="100%" height="100%">
        <Treemap width={400} height={200} data={data} dataKey="dividends" aspectRatio={4 / 3} stroke="#fff" fill="#8884d8" />
      </ResponsiveContainer>
    </div>
  )
}