import React, { useState, useEffect } from 'react';
import { HistoricalPriceEntity, Info } from "./models";
import { LineChart, Line, XAxis, YAxis, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { get_historical_price, get_info } from './utils';
import { useParams } from 'react-router-dom';

export default function HistoricalPrice() {
  const [data, setData] = useState<{ x: string, y: number }[]>([]);
  const [currency, setCurrency] = useState<string>("$");
  const params = useParams<{ id: string, market: string }>();

  useEffect(() => {
    get_historical_price(params.market, params.id).then(response => {
      let prices = response as HistoricalPriceEntity[]
      var data = []
      // convert date
      for (let item of prices) {
        let d = new Date(item.Date)
        data.push({
          x: d.toLocaleDateString(),
          y: item.Close,
        })
      }
      setData(data)
    })

    get_info(params.market, params.id).then(response => {
      let info = response as Info
      setCurrency(info.currency)
    })
  }, [params.id, params.market])

  return (
    <div style={{ width: 800, height: 600 }}>
      <ResponsiveContainer>
        <LineChart
          width={800}
          height={600}
          data={data}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5
          }}
        >
          <XAxis dataKey="x" />
          <YAxis tickFormatter={d => `${currency} ${d}`} />
          <Tooltip />
          <Legend />
          <Line
            type="monotone"
            dataKey="y"
            name="Historical Price"
            stroke="#8884d8"
            dot={false}
          />
        </LineChart>
      </ResponsiveContainer>
    </div>
  )
}